import { createRoot } from 'react-dom/client';
import App from './app';
import env from './utils/environment';
import * as dayjs from 'dayjs';
import 'dayjs/locale/nb';
import './index.less';
import '@navikt/ds-css';

dayjs.locale('nb');

const renderApp = () => createRoot(document.getElementById('root')!).render(<App />);

if (env.isLocal) {
	console.log('Starting mock service worker');
	console.log('import.meta.url', import.meta.url);

	const { worker } = await import('./mock/index');
	console.log(worker);
	console.log('Mock service worker started');
	worker
		.start({ serviceWorker: { url: `${import.meta.env.BASE_URL}mockServiceWorker.js` } })
		.then(() => {
			renderApp();
		})
		.catch((e: Error) => {
			console.error('Unable to setup mocked API endpoints', e);
		});
} else {
	renderApp();
}
